import { Injectable, Output, EventEmitter } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { Title } from '@angular/platform-browser';
import 'rxjs/add/operator/map';

@Injectable()
export class SpotifyService{
    private searchUrl: string;
    private artistUrl: string;
    private albumsUrl: string;
    private albumUrl: string;
    private audio: HTMLAudioElement;
    private encode = 'Y2U2NjYyOGEyZjI3NDQwMGIxYTViZWM2YWY0NTY4MGI6MjYxOGY2ZmVhODFmNDY0ZWE3NTE4ZjQ5M2NmZmNhNmQ=';
    private header;
    app_name = 'SpotifyLite';
    status: EventEmitter<string> = new EventEmitter();

    constructor(private _http: Http, private titleService: Title) {
        this.titleService.setTitle( this.app_name );
        this.getAuthorizationHeader();
        setInterval(() => {
            this.getAuthorizationHeader();
        }, 60000);
    }

    getAuthorizationHeader() {
        let header = new Headers();
        header.append('Authorization', 'Basic ' + this.encode);
        header.append('Content-Type', 'application/x-www-form-urlencoded');
        const header_final = {
            headers: header
        };
        const postUrl = 'https://accounts.spotify.com/api/token';
        this._http.post(postUrl, 'grant_type=client_credentials', header_final).subscribe(res => {
            header = new Headers();
            header.append('Authorization', 'Bearer ' + res.json().access_token);
            this.header = {
                headers: header
            };
            console.log('Token Obtained');
        }, err => {
            setTimeout(() => {
                this.getAuthorizationHeader();
            }, 500);
        });
    }

    handleAuthorizationError() {
        alert('Authorization Token Expired, please resubmit your request');
        this.getAuthorizationHeader();
    }

    searchMusic(str: string, type = 'artist') {
        this.searchUrl = 'https://api.spotify.com/v1/search?query=' + str + '&offset=0&limit=10&type=' + type + '&market=US';
        return this._http.get(this.searchUrl, this.header).map(res => res.json(), err => alert('No Internet Connection'));
    }

    getArtist(artistId: string) {
        this.artistUrl = 'https://api.spotify.com/v1/artists/' + artistId;
        return this._http.get(this.artistUrl, this.header).map(res => res.json(), err => alert('No Internet Connection'));
    }

    getAlbums(artistId: string) {
        this.albumsUrl = 'https://api.spotify.com/v1/artists/' + artistId + '/albums';
        return this._http.get(this.albumsUrl, this.header).map(res => res.json(), err => alert('No Internet Connection'));
    }

    getAlbum(artistId: string) {
        this.albumUrl = 'https://api.spotify.com/v1/albums/' + artistId;
        return this._http.get(this.albumUrl, this.header).map(res => res.json(), err => alert('No Internet Connection'));
    }

    preview(url: string) {
        if (this.audio) {
            this.audio.pause();
        }
        if (url) {
            this.audio = new Audio(url);
            this.audio.play();
            this.audio.onended = () => {
                this.status.emit('ended');
            };
            this.audio.onplaying = () => {
                this.status.emit('ready');
            };
        }
    }
}



